  /**
   * Location type.
   * @readonly
   */
  locationType?: LocationTypeWithLiterals;
  /**
   * Stores location name.
   * @readonly
   * @minLength 1
   * @maxLength 150
   */
  name?: string;
  /**
   * Whether the location is the site's default location.
   * @readonly
   */
